#content, #fog-container {
  font-family: 'InterstateMono', 'Courier New', courier, monospace;
  position: absolute;
  top: 0;
  width: 100%;
  padding: 10px;
  box-sizing: border-box;
  text-align: center;
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  pointer-events: none;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  z-index: 10001;  color: white;
  font-weight: 400;
  font-size: 14px;
}
