.is-closed {
  -webkit-filter: url(#blur);
  filter: url(#blur);
  -webkit-filter: blur(3px);
  filter: progid:DXImageTransform.Microsoft.Blur(PixelRadius='3');
  filter: blur(3px);
}

.is-active {
  background: linear-gradient(180deg, #FFFFFF 0%, #96D0EC 100%), #FFFFFF;
  border: 0.5px solid #84B5CB;
  box-sizing: border-box;
  box-shadow: inset 0px -2px 2px #96D0EC;
}

.dg {
  display: none;
}

.wordmark {
  mix-blend-mode: multiply;
}

.connect-wallet {
  background: #FFFFFF;
  border: 0.5px solid #84B5CB;
  box-sizing: border-box;
  box-shadow: inset 0px -2px 2px #96D0EC;
  border-radius: 15px;
  padding: 10px 15px;
  float: left;
}


.is-not-farming {
  margin-left: 12.5%;
}

.is-farming {
  //position: relative;
  width: 55vw !important;
  height: 55vw !important;

  float: left;
  margin-left: 2.5%;
}

.is-farming:nth-child(2) {
  margin-left: 25%;
}


.section-label {
  width: 100%;
  text-align: left;
  padding-left: 15px;
}

.token-input {
  width: 100%;
  margin-bottom: 10px;

  input {
    width: 75%;
    float: left;

    background: #FFFFFF;
    border: 0.5px solid #84B5CB;
    box-sizing: border-box;
    box-shadow: inset 0px -2px 2px #96D0EC;
    border-radius: 15px;

    padding: 10px 15px;
  }

  .max {
    width: 22.5%;
    margin-left: 2.5%;
  }

}

.lp-tokens {

  padding-bottom: 20px;

  .push-5 {
    margin-right: 2.5%;
  }

}

.rewards{
	padding-top: 5px;

}
