body {
  margin: 0;
  background-color: #83BDDD;
  color: #fff;
  font-family: 'InterstateMono', 'Courier New', courier, monospace;
  font-size: 13px;
  line-height: 24px;
  overscroll-behavior: none;
}

a {
  color: #ff0;
  text-decoration: none;

  &:hover {
    text-decoration: underline;
  }
}

h1, h2, h3 {
  font-weight: 400;
}

h2 {
  font-size: 26px;
}

h3 {
  font-size: 20px;
}

strong {
  background-color: #96D0EC;
  font-weight: 400;
  padding: 5px;
  border-radius: 5px;
}

button {
  cursor: pointer;
  text-transform: uppercase;
}

canvas {
  display: block;
}


a, button, input, select {
  pointer-events: auto;
}

header {
  position: fixed;
  width: 97%;
  left: 1.5%;
  top: 1.5vw;

  z-index: 100001;
}

//mobile-menu

nav {
  position: relative;
  float: right;

  background: #FFFFFF;
  border: 0.5px solid #84B5CB;
  box-sizing: border-box;
  box-shadow: inset 0px -2px 2px #96D0EC;
  border-radius: 15px;

  padding: 5.5px 20px;

  ul {
    list-style: none;

    padding: 0;
    margin-block-start: 0;
    margin-block-end: 0;
  }

  li:first-child {
    padding-left: 0;
  }

  li {
    float: left;
    padding-left: 20px;

    color: black;
  }

}

footer {
  margin: 0 auto;
  z-index: 10001;

}

footer a {
  padding-right: 15px;
}

a {
  color: black;
}


canvas {
  position: fixed;
}
pre {
  background: #f4f4f4;
  border: 1px solid #ddd;
  border-left: 3px solid #f36d33;
  color: #666;
  page-break-inside: avoid;
  font-family: 'InterstateMono', 'Courier New', courier, monospace;
  font-size: 15px;
  line-height: 1.6;
  margin-bottom: 1.6em;
  max-width: 100%;
  overflow: auto;
  padding: 1em 1.5em;
  display: block;
  word-wrap: break-word;
}