
.logo img{
  z-index: 1001;
  top:100px;
  width: 80%;
  margin-top:1.5%;
  mix-blend-mode: luminosity;
  padding-bottom:25px;
}

@media screen and (max-width: 992px) {
  .logo img{
    width: 90%;
  }
}


.logo-top{
  width:135px;
  margin:0 auto;
  margin-top:5.5%;

  mix-blend-mode: multiply;
  -webkit-blend-mode: multiply;
}

.logo-top-smaller{
  width:55px;
  margin:0 auto;
  margin-top:1.5%;

  mix-blend-mode: multiply;
  -webkit-blend-mode: multiply;
}
