.stats {
  &-panel{
    background: #FFFFFF;
    color: black;
    border: 0.5px solid #84B5CB;
    box-sizing: border-box;
    box-shadow: inset 0px -2px 2px #96D0EC;
    border-radius: 15px;
    padding: 10px 15px;
    width: 47.5%;
    float: left;
    margin-left: 1.25%;
    margin-bottom: 1.25%;
    &.rank{
      margin: 0 auto;
      clear: both;
      float: none;
    }
  }
  &-label{
    color: #555555;
    border-bottom: 2px solid #efefef;
    border-radius: 1px;
    margin-bottom: 15px;
  }
  &-info{
    padding-bottom: 5px;
  }

}
