.button-container{
  margin-top: 15px;
}

.button{
  background: #FFFFFF;
  border: 0.5px solid #84B5CB;
  box-sizing: border-box;
  box-shadow: inset 0px -2px 2px #96D0EC;
  border-radius: 15px;

  padding: 10px 15px;
}

.button:hover{
  background-color: #efefef;
}

.button:focus {
  outline: none;
  box-shadow: none;
}


.btn-half {
  width: 48.75%;
  float: left;
}

.btn-full {
  width: 100%;
}