.ctr{
  float: left;
  width: 30%;
  margin-left:35%;
  z-index: 1001;
  position: relative;

  p{
    float: none;
    color:black;
    text-align: center;
    width: 100%;

    z-index: 100001;

    margin:0 auto;
    height:35px;

    margin-top:100px;
    background: #FFFFFF;
    border: 0.5px solid #84B5CB;
    box-sizing: border-box;
    box-shadow: inset 0px -2px 2px #96D0EC;
    border-radius: 15px;

    padding: 5px 15px;
  }
}

@media screen and (max-width: 992px) {
  .ctr{
    width: 100%;
    margin-left:0%;
  }
}
