@import 'helpers/shadows';
.fog{

  width: 85vw !important;
  height:45vw !important;

  margin-top:30px;

  //margin-left: -3vw !important;
  margin: 0 auto !important;
  display: contents;

  .pool-info{
    width: 85%;
    min-width: 360px;
    max-width: 600px;
    margin: 0 auto;
  }

  .pool-inner{
    grid-template-columns: 0.75fr;
    background-repeat: no-repeat;
  }

  #pool-w-d{
    display: grid;
    margin-top: 30px;
  }

  .button{
    background: #0C1B20;
    color: white;
    border: 0.5px solid #16313A;
    box-sizing: border-box;
    box-shadow: inset 0px -2px 2px #1C2E32;

    &:hover{
      background: #162427;
    }

  }

  .stats-panel{
    background: #0C1B20;
    color: white;
    border: 0.5px solid #16313A;
    box-sizing: border-box;
    box-shadow: inset 0px -2px 2px #1C2E32;
  }

  .stats-label{
    border-bottom: 2px solid #444;
    color: #888;
  }

  input{
    background: #0C1B20;
    color: white;
    border: 0.5px solid #16313A;
    box-sizing: border-box;
    box-shadow: inset 0px -2px 2px #1C2E32;
  }

}
.shadow-text{
  text-shadow: 0px 2px 4px #234D3C;
}
.pool-api{
  padding-top:5px;
}

#pool-stats{
  margin-top:20px;
}

.deposit-withdraw{
  margin-top:40px;
}

.stats-inner{
  margin-top:20px;
  display: flow-root;
}

.fog .pool-inner{
  background-image: url('../images/fog.png');
}

//condensate-panel

.condensate{
  .condensate-panel{
    background: #0C1B20;
    color: white;
    border: 0.5px solid #888;
    box-sizing: border-box;
    box-shadow: inset 0px -2px 2px #1C2E32;
    border-radius: 15px;
    padding: 20px 15px;
    width: 100%;
    margin: 40px auto;// FIXME:
  }

  .condensate-label{
    color:#efefef;
    border-bottom: 2px solid #444;
    border-radius: 1px;
    margin-bottom: 15px;
  }

  .condensate-info{
    padding-bottom:5px;
  }

  .condensate-logo{
    width: 40px !important;
  }
}



@media screen and (max-width: 992px) {

  .pool{
    width: 90vw !important;
    height:90vw !important;
  }

  canvas{
    display: none;
  }

  .about-info{
    width: 95%;
    margin:0 auto;

    padding: 50px 50px 95px 50px;

    margin-top: 10vh;
    text-align: left;


    color: black;

  }

  .fog .pool-inner{
    background-image: none !important;
  }

  .stats-panel{
    width: 100%;
    margin-left:0%;
  }


}