
.farming-pop-up{
  background: #FFFFFF;
  border: 0.5px solid #84B5CB;
  box-sizing: border-box;
  box-shadow: inset 0px -2px 2px #96D0EC;
  border-radius: 15px;

  padding: 10px 15px;

  width: 500px;
  height: 300px;

  color: black;
}

.pop-up-content{
  width: 90%;
  margin:0 auto;

  padding-top: 15px;

  .push-5{
    margin-right: 2.5%;
  }

}

.pop-up-title{
  margin-bottom: 25px;
}

.pop-up-note{
  width: 90%;
  margin-top:45px;

  float: left;
}


.farming-pop-up{
  display: none;
  position: fixed;
  top: calc(50% - 150px);
  left: calc(50% - 250px);
}
