
.pool-inner {
  background-image: url('../../images/puddle-f-1-1.png');
  background-size: contain;
  width: 100%;
  height: 100%;
  display: inline-grid;
  justify-content: center;
  align-content: center;
  grid-template-columns: 0.5fr;
}



.pool-tabs {
  margin-bottom: 25px;
  width: 100%;
}

//Pools

#pools {
  padding-top: 55px;
}

.pools {
  width: 80%;
  margin-left: 10%;
  margin-top: 15px;
}


.pool {
  width: 25vw !important;
  height: 25vw !important;
  float: left;
  margin-left: 2.5%;
  &-2{
    margin-top: 8.5vh;
    margin-left: 11.5vw;
    &.pool-inner{
        background-image: url('../../images/puddle-f-2.png');
    }
  }
  &-3{
    margin-top: 3.5vh;
    margin-left: 1.5vw;
    &.pool-inner{
      background-image: url('../../images/puddle-f-3.png');
    }
  }
  &-4{
    margin-top: 6.5vh;
    margin-left: 8.5vw;
    &.pool-inner{
      background-image: url('../../images/puddle-f-4.png');
    }
  }
}

.pools-info-panel {
  a {
    color: white;
  }
}


@media screen and (max-width: 992px) {

  .pools {
    width: 90%;
    margin-left: 5%;
  }

  .pool {
    width: 100%;

    img {
      width: 95%;
    }

  }
}

.puddle {
  //position: absolute;
}

.pool-info {

  padding-bottom: 15%;

  h2, h3 {
    margin-block-start: 0;
    margin-block-end: 0;
    font-family: 'InterstateMono', courier, monospace;
    font-weight: 600;
  }

  h3 {
    font-weight: 400;
  }

  .pool-api {
    padding-bottom: 20px;
  }

}
