
@media screen and (max-width: 992px) {
  .hide-on-mobile {
    display: none;
  }
}

@media screen and (min-width: 993px) {
  .mobile-menu, .mobile-menu-button {
    display: none;
  }
}

.mobile-menu-button {
  float: right;
}

.mobile-menu {
  display: none;
  position: fixed;
  z-index: 100001;
  top: 50px;

  width: 97%;

  ul {
    padding-inline-start: 0;
  }

  li {
    width: 100%;
    padding: 0 10px !important;
    text-align: center;
  }
}
